import React from 'react';
import './Todo.css';
import {useState} from 'react'
function Todo() {
    const [todos,setTodos]=useState([])
    const [todo,setTodo]=useState('')
     console.log(todos)
     const handlechange=(e)=>{
      setTodos([...todos,todo ])
      e.preventDefault()
      setTodo("");

  
     }
     const deleteicon = id =>{
         console.log(id)
        const settodo = todos.filter((value,index) => {
          return index !== id;
        });
        setTodos(settodo);
        /*const newTodos = [...todos];
      newTodos.splice(index,1);
      setTodos(newTodos);*/
     }
  return(  
  <div className='todo-cont'> 
       
    <form className='input-sec' onSubmit={ handlechange  }>
    <h1>Todo App</h1>
      
        <input  value={todo}  onChange={(e)=> setTodo(e.target.value ) }   type="text" placeholder='Enter items' 
        />
       

    </form>
    { todos.map((value,index)=>{
     return(
     <div> 
     <ul>
        <li><input type="checkbox" /> {value} <i className="fas fa-trash-alt" onClick={()=>deleteicon(index)}></i></li>
   </ul>
    </div>)})}
  </div>
  );
}

export default Todo;
